import React from 'react';
import '../styles/Footer.css';
import { Link } from 'react-router-dom';

import { useTranslation } from 'react-i18next';

const Footer = () => {

const { t, i18n } = useTranslation(); // Get the t and i18n objects
  return (
    <footer className="footer">
      <div className="footer-content">
        <div className="footer-logo">
          <Link to="/" className="nav-link">
                          <img src="/images/logo.png" alt="Your Logo" style={{ width: '125px', height: '64px' }} />
                        </Link>
        </div>
        <div className="footer-address">
          {t('Address')}
        </div>
      </div>
            <div className="footer-social">
              {/* Image icon for Freelancer */}
              <a href="https://www.freelancer.com/manage/jobs/projects/open" className="footer-icon">
                <img src="/freelencer.png" alt="Freelancer" />
              </a>
            </div>
    </footer>
  );
};

export default Footer;
